import * as Yup from 'yup';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { useEffect, useState } from 'react';

import Link from '@mui/material/Link';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import LoadingButton from '@mui/lab/LoadingButton';

import { paths } from 'src/routes/paths';
import { RouterLink } from 'src/routes/components';
import { useTranslate } from 'src/locales';

import { EmailInboxIcon } from 'src/assets/icons';

import { useSnackbar } from 'src/components/snackbar';
import { useAuthContext } from 'src/auth/hooks';
import Iconify from 'src/components/iconify';
import FormProvider, { RHFCode } from 'src/components/hook-form';
import axios, { endpoints } from 'src/utils/axios';

// ----------------------------------------------------------------------

export default function ModernVerifyView() {
  const { t } = useTranslate();
  const { user, logout } = useAuthContext();

  const [countdown, setCountdown] = useState(120);
  const [canResend, setCanResend] = useState(false);

  const formatTime = (time: number) => {
    const minutes = String(Math.floor(time / 60)).padStart(2, '0');
    const seconds = String(time % 60).padStart(2, '0');
    return `${minutes}:${seconds}`;
  };

  const VerifySchema = Yup.object().shape({
    otp: Yup.string()
      .min(6, t('verification.otp.minLength'))
      .required(t('verification.otp.required')),
  });

  const defaultValues = {
    otp: '',
  };

  const methods = useForm({
    mode: 'onChange',
    resolver: yupResolver(VerifySchema),
    defaultValues,
  });

  const {
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const { enqueueSnackbar } = useSnackbar();

  useEffect(() => {
    if (countdown > 0) {
      const timer = setTimeout(() => setCountdown(countdown - 1), 1000);
      return () => clearTimeout(timer);
    }
    setCanResend(true);
    return undefined;
  }, [countdown]);

  const onSubmit = handleSubmit(async (data) => {
    try {
      await axios.post(endpoints.auth.verify, data);
      enqueueSnackbar(t('verification.accountVerified'));
      window.location.reload();
    } catch (error) {
      const errorMessage =
        typeof error?.response?.data === 'string'
          ? error?.response?.data
          : error?.response?.data?.message;
      enqueueSnackbar(errorMessage, {
        variant: 'error',
      });
    }
  });

  const resend_otp = async () => {
    try {
      await axios.post(endpoints.auth.resend_otp);
      enqueueSnackbar(t('verification.otpResent'));
      setCountdown(120);
      setCanResend(false);
    } catch (error) {
      const errorMessage =
        typeof error?.response?.data === 'string'
          ? error?.response?.data
          : error?.response?.data?.message;
      enqueueSnackbar(errorMessage, {
        variant: 'error',
      });
    }
  };

  const renderForm = (
    <Stack spacing={3} alignItems="center">
      {formatTime(countdown)}
      <RHFCode name="otp" />

      <LoadingButton
        fullWidth
        size="large"
        type="submit"
        variant="contained"
        color="primary"
        loading={isSubmitting}
      >
        {t('verification.verify')}
      </LoadingButton>

      <Typography variant="body2">
        {`${t('verification.noCode')} `}
        <Link
          variant="subtitle2"
          sx={{
            cursor: canResend ? 'pointer' : 'not-allowed',
            pointerEvents: canResend ? 'auto' : 'none',
            color: canResend ? 'primary.main' : 'text.secondary',
          }}
          onClick={resend_otp}
        >
          {t('verification.resendCode')}
        </Link>
      </Typography>

      <Link
        component={RouterLink}
        onClick={() => {
          logout();
        }}
        href={paths.auth.jwt.login}
        color="inherit"
        variant="subtitle2"
        sx={{
          alignItems: 'center',
          display: 'inline-flex',
        }}
      >
        <Iconify icon="eva:arrow-ios-back-fill" width={16} />
        {t('verification.returnToSignIn')}
      </Link>
    </Stack>
  );

  const renderHead = (
    <>
      <EmailInboxIcon sx={{ height: 96 }} />

      <Stack spacing={1} sx={{ mt: 3, mb: 1 }}>
        <Typography variant="h3">{t('verification.checkSms')}</Typography>

        <Typography variant="body2" sx={{ color: 'text.secondary' }}>
          {t('verification.confirmationCode', { phone: user?.phone })}
        </Typography>
      </Stack>
    </>
  );

  return (
    <FormProvider methods={methods} onSubmit={onSubmit}>
      {renderHead}

      {renderForm}
    </FormProvider>
  );
}
